import { message } from 'antd'
import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { Button, Container, Form, Modal, Row, Table } from 'react-bootstrap'
import { useSelector } from 'react-redux'
import qrCode from '../../assets/qrcodes/adiNathMiniQr.png'
import { Navigate } from 'react-router-dom'

const Index = () => {
  const user = useSelector((state) => state.user.user)
  const [data, setData] = useState([])
  const [show, setShow] = useState(false)
  const [modalRole, setModalRole] = useState('')
  const [modalAmount, setModalAmount] = useState('')
  const [transactionId, setTransactionId] = useState('')

  const handleShow = (key, value) => {
    setModalRole(key)
    setModalAmount(value)
    setShow(true)
  }

  const handleClose = () => {
    setShow(false)
  }

  const getRolePrice = async () => {
    try {
      const role = user.role
      const res = await axios.post('api/v1/user/getRolePrice', { role })
      if (res.data.success) {
        setData(Object.entries(res.data.data))
      }
    } catch (error) {
      console.log(error)
      message.error(error.response.data.message)
    }
  }
  const handleSUbmit = async (e) => {
    try {
      e.preventDefault()
      e.stopPropagation()
      if (!transactionId) {
        handleClose()
        return message.error('Please enter a transaction ID')
      }
      const userId = user.uniqueId
      const res = await axios.post('api/v1/user/changeRoleReq', { userId, modalRole, transactionId,modalAmount })
      if (res.data.success) {
        handleClose()
        message.success('Request for role change has been made successfully')
      }
    } catch (error) {
      console.log(error)
      message.error(error.response.data.message)
    }
  }

  useEffect(() => {
    getRolePrice()
  }, [])

  return (
    <Container fluid>
      <Row>
        <h1>Role Change Price</h1>
      </Row>
      <div className="row">
        <div className="col-md-10">
          <div className="card">
            <div className="card-header">
              <h3 className="card-title">Roles</h3>
            </div>
            <Table responsive className="ms-3">
              <thead>
                <tr>
                  <th>SN</th>
                  <th>Role</th>
                  <th>Amount</th>
                  <th>Buy</th>
                </tr>
              </thead>
              <tbody>
                {data && data.length > 0 ? (
                  data.map(([key, value], index) => (
                    <tr key={key}>
                      <td>{index + 1}</td>
                      <td>
                        {key === 'masterDistributor'
                          ? 'Master Distributor'
                          : key.charAt(0).toUpperCase() + key.slice(1)}
                      </td>
                      <td>{value}</td>
                      <td>
                        <Button onClick={() => handleShow(key, value)} className="myCustomBtn">
                          Buy
                        </Button>
                      </td>
                    </tr>
                  ))
                ) : (
                  <tr>
                    <td colSpan="4" className="text-center text-danger">
                      {data && data ? 'No Data Found' : 'Loading...'}
                    </td>
                  </tr>
                )}
              </tbody>
            </Table>
          </div>
        </div>
      </div>
      <Modal show={show} onHide={handleClose} animation={false}>
        <Modal.Header closeButton>
          <Modal.Title>Make Request for Role Change</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <p>
            By clicking on Submit button your request for{' '}
            <b>{modalRole === 'masterDistributor' ? 'Master Distributor' : modalRole}</b> Role will
            be made.
          </p>
          <p>
            Pay the <b>â‚¹{modalAmount}</b> by scanning the QR Code and enter your Transaction ID and
            submit the request.
          </p>
          <div className="d-flex align-items-center justify-content-center">
            <img src={qrCode} alt="" />
          </div>
          <Form>
            <Form.Group>
              <Form.Label>Transaction ID</Form.Label>
              <Form.Control
                type="text"
                value={transactionId}
                placeholder="Enter Transaction ID"
                onChange={(e) => setTransactionId(e.target.value)}
              />
            </Form.Group>
            <div className="d-flex justify-content-end m-3">
              <Button type="submit" className="myCustomBtn" onClick={handleSUbmit}>
                Submit
              </Button>
            </div>
          </Form>
        </Modal.Body>
      </Modal>
    </Container>
  )
}

export default Index

